import React, { useState, useContext, useEffect } from "react";
import Profile from "./Profile";
import Category from "./Category";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../firebase/AuthContext ";
import { signInWithEmailAndPassword, signOut } from "firebase/auth";
import { auth } from "../firebase/firebaseConfig";
import { getDatabase, ref, onValue } from "firebase/database";

const SideMenu = () => {
    const [visitors, setVisitors] = useState(0);
    const [dailyVisitor, setDailyVisitor] = useState(0);
    const { currentUser } = useContext(AuthContext); //  
    const navigate = useNavigate();

      useEffect(() => {
        const db = getDatabase();
        const visitorsRef = ref(db, "visitors");
        const dailyVisitorsRef = ref(db, "dailyVisitors");
        // setTest(visitorsRef);
        // console.log(' : ', visitorsRef);
    
        const visitorsCounter = onValue(visitorsRef, (snapshot) => {
          const data = snapshot.val();
          // console.log('data1 !!! : ', data);
          setVisitors(data);
        });

        const dailyVisitorsCounter = onValue(dailyVisitorsRef, (snapshot) => {
          const data = snapshot.val();
          setDailyVisitor(data);
        })
    
        return () => {visitorsCounter(); dailyVisitorsCounter();}
      }, []);

    const handleLogout = async () => { //
        try {
          await signOut(auth);
          alert(" !");
        } catch (error) {
          console.error(error);
          alert(" !");
        }
      };

    const PrintButton = () => {
        if (!currentUser) { //  
            return <SideButton onClick={() => {navigate('/login')}}></SideButton>
        } else if (currentUser) { //  
            return (
                <div>
                    <SideButton onClick={() => {navigate('/post')}}> </SideButton>
                    <SideButton onClick={() => {handleLogout()}}></SideButton>
                </div>
            )
        } else {
            return <p>!</p>
        };
    };

    return (
        <div style={{marginTop: '1rem'}}>
          {/* <p style={{fontWeight:'bold', color:'white'}}>  (RightMenu.js)</p> */}
          <Profile />
          <PrintButton />
          <Category />
          <VisitorContainer>
            <VisitorText style={{borderBottomRightRadius:'0', borderTopRightRadius:'0'}}>Today</VisitorText>
            <VisitorText style={{borderBottomLeftRadius:'0', borderTopLeftRadius:'0', backgroundColor:'skyblue', color: 'white'}}>{dailyVisitor}</VisitorText>
            <VisitorText style={{borderBottomRightRadius:'0', borderTopRightRadius:'0', marginLeft:'1rem'}}>Total</VisitorText>
            <VisitorText style={{borderBottomLeftRadius:'0', borderTopLeftRadius:'0', backgroundColor:'skyblue', color: 'white'}}>{visitors}</VisitorText>
          </VisitorContainer>
        </div>
    );
};

export default SideMenu;

const SideButton = styled.button`
  padding:5px 10px 5px 10px;
//   min-width: 50px;
  width : 99%;
  height: auto;
  border-radius: 2px;
  font-size: 18px;
  font-weight: 700;
  background-color: gray;
  border: 2px solid black;
  color: white;
  cursor: pointer;
  margin-top: 5px;
  margin-bottom: 5px;
`;

const VisitorContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0.5rem 0 0 0;
`;
const VisitorText = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: bold;
  background-color: gray;
  color: white;
  height: 1.5rem;
  padding: 0 0.4rem 0 0.4rem;
  border-radius: 2px;
  margin: 0;
`