import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Link, useNavigate } from 'react-router-dom'; //    
import { getDatabase, ref, get, set } from 'firebase/database';
import './App.css';
import styled from 'styled-components';
import Home from './components/Home';
import Header from './components/Head/Header';
import Tail from './components/Head/Tail';
import Test from './components/test';
import Test2 from './components/test2';
import ErrorPage from './components/ErrorPage';
import SideMenu from './components/side_menu/SideMenu';
import List from './components/firebase/List';
import Post from './components/firebase/Post';
import Login from './components/firebase/Login';
import SignUp from './components/firebase/SignUp';
import Detail from './components/firebase/Detail';
import Edit from './components/firebase/Edit';
import PortfolioHome from './components/Portfolio/PortfolioHome';

import { AuthProvider } from './components/firebase/AuthContext ';

//   
const Layout = ({ children }) => {
  return (
    <Container>
      <div style={{ width: '80%'}}>
        <Header />
        <div style={{ display: 'flex', flexDirection:'row', justifyContent: 'center' }}>
          <div style={{ flex: 2 }}>
            <SideMenu />
          </div>

          {/* <hr style={{ width: '1px', backgroundColor: 'black', border: 'none', marginLeft: 10, marginRight: 10 }} /> */}

          <div style={{ flex: 8 }}>
            {children}  {/*       */}
          </div>
        </div>
      </div>
      <Tail/>
    </Container>
  );
};

const App = () => {
  const VisitorCount = async () => { // 
    const db = getDatabase();
    const visitorsRef = ref(db, "visitors"); // 
    const dailyVisitorsRef = ref(db, "dailyVisitors"); // ( )
  
    await get(visitorsRef).then(async (snapshot) => {
      if (snapshot.exists()) {
        //   1 
        await set(visitorsRef, snapshot.val() + 1);
      } else {
        // 
        await set(visitorsRef, 1);
      }
    });

    await get(dailyVisitorsRef).then(async (snapshot) => {
      if (snapshot.exists()) {
        //   1 
        await set(dailyVisitorsRef, snapshot.val() + 1);
      } else {
        // 
        await set(dailyVisitorsRef, 1);
      }
    });
  };
  


  useEffect(() => {
    VisitorCount();
  }, []);

  return (
    <div style={{fontSize: 25, textAlign: 'center'}}>
      <AuthProvider>
        <BrowserRouter basename='/blog'>
          <Routes>
            <Route 
            path='/'
            element={
              <Layout>
                {/* <Home /> */}
                <List />
              </Layout>
            }
            />
            <Route path='post' element={ // 
                <Layout>
                  <Post />
                </Layout>
            }/>
            <Route path='list' element={ // 
                <Layout>
                  <List />
                </Layout>
            }/>
            <Route path="list/:id" element={ // 
                <Layout>
                  <Detail />
                </Layout>
            }/>
            <Route path="edit/:id" element={ // 
                <Layout>
                  <Edit />
              </Layout>
            }/>
            <Route path='signup' element={ //
                <Layout>
                  <SignUp />
                </Layout>
            }/>
            <Route path='login' element={ //  
                <Layout>
                  <Login />
                </Layout>
            }/>
            <Route path='portfolio' element={
                <Layout>
                  <PortfolioHome />
                </Layout>
            }/>
            <Route path='test1' element={
                <Layout>
                  <Test />
                </Layout>
            }/>
            <Route path='test2' element={
                <Layout>
                  <Test2 />
                </Layout>
            }/>
            <Route path='*' element={ //    
              <ErrorPage />
            } />
          </Routes>
        </BrowserRouter>
      </AuthProvider>
    </div>
  );
};

export default App;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // height: 100vh;
  // background-color: lightgray;
`;
